.cell:hover {
    -webkit-transform: scaleX(1.1) scaleY(1.1);
            transform: scaleX(1.1) scaleY(1.1);
    transition: -webkit-transform 0.1s ease-in-out;
    transition: transform 0.1s ease-in-out;
    transition: transform 0.1s ease-in-out, -webkit-transform 0.1s ease-in-out;
}

.apple::before {
    content: "";
    background-image: url(/ia-snake-react/static/media/apple.a7b55597.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    display: block;
    z-index: 0;
    width: 100%;
    height: 100%;
    -webkit-animation: appleAnimation 0.7s alternate infinite ease-in;
            animation: appleAnimation 0.7s alternate infinite ease-in;
}

@-webkit-keyframes appleAnimation {
    0% {
        -webkit-transform: scale(.9);
                transform: scale(.9);
    }
    100% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
}

@keyframes appleAnimation {
    0% {
        -webkit-transform: scale(.9);
                transform: scale(.9);
    }
    100% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
}

