.expanded::after,
.explored::after,
.path::after {
    width: 100%;
    height: 100%;
    content: '';
    display: flex;
    justify-content: center;
    align-content: center;
}

.path::after {
    background-color: rgb(255, 255, 255);
    -webkit-transform: scale(0.6, 0.6);
            transform: scale(0.6, 0.6);
    -webkit-animation: pathanimation 0.5s ease-in;
            animation: pathanimation 0.5s ease-in;
}

.explored::after {
    -webkit-animation: exploredanimation 0.6s ease-in;
            animation: exploredanimation 0.6s ease-in;
    -webkit-transform: scale(0.7, 0.7);
            transform: scale(0.7, 0.7);
    background-color: rgb(255, 238, 0);
}

@-webkit-keyframes exploredanimation {
    0% {
        border-radius: 50%;
        -webkit-transform: scale(0.8, 0.8);
                transform: scale(0.8, 0.8);
    }
    60% {
        border-radius: 25%;
        -webkit-transform: scale(0.5, 0.5);
                transform: scale(0.5, 0.5);
    }
    100% {
        border-radius: 0%;
        background-color: rgba(241, 228, 39, 0.829);
        -webkit-transform: scale(0.7, 0.7);
                transform: scale(0.7, 0.7);
    }
}

@keyframes exploredanimation {
    0% {
        border-radius: 50%;
        -webkit-transform: scale(0.8, 0.8);
                transform: scale(0.8, 0.8);
    }
    60% {
        border-radius: 25%;
        -webkit-transform: scale(0.5, 0.5);
                transform: scale(0.5, 0.5);
    }
    100% {
        border-radius: 0%;
        background-color: rgba(241, 228, 39, 0.829);
        -webkit-transform: scale(0.7, 0.7);
                transform: scale(0.7, 0.7);
    }
}

@-webkit-keyframes expandedanimation {
    0% {
        border-radius: 50%;
        -webkit-transform: scale(0, 0);
                transform: scale(0, 0);
        background-color: rgba(114, 102, 218, 0.794);
    }
    60% {
        border-radius: 25%;
        -webkit-transform: scale(0.9, 0.9);
                transform: scale(0.9, 0.9);
    }
    100% {
        border-radius: 0%;
        -webkit-transform: scale(0.7, 0.7);
                transform: scale(0.7, 0.7);
        background-color: rgba(172, 73, 175, 0.794);
    }
}

@keyframes expandedanimation {
    0% {
        border-radius: 50%;
        -webkit-transform: scale(0, 0);
                transform: scale(0, 0);
        background-color: rgba(114, 102, 218, 0.794);
    }
    60% {
        border-radius: 25%;
        -webkit-transform: scale(0.9, 0.9);
                transform: scale(0.9, 0.9);
    }
    100% {
        border-radius: 0%;
        -webkit-transform: scale(0.7, 0.7);
                transform: scale(0.7, 0.7);
        background-color: rgba(172, 73, 175, 0.794);
    }
}

@-webkit-keyframes pathanimation {
    0% {
        -webkit-transform: scale(0, 0);
                transform: scale(0, 0);
    }
    60% {
        -webkit-transform: scale(1.1, 1.1);
                transform: scale(1.1, 1.1);
    }
    100% {
        -webkit-transform: scale(0.6, 0.6);
                transform: scale(0.6, 0.6);
    }
}

@keyframes pathanimation {
    0% {
        -webkit-transform: scale(0, 0);
                transform: scale(0, 0);
    }
    60% {
        -webkit-transform: scale(1.1, 1.1);
                transform: scale(1.1, 1.1);
    }
    100% {
        -webkit-transform: scale(0.6, 0.6);
                transform: scale(0.6, 0.6);
    }
}

.expanded::after {
    background-color: rgba(172, 73, 175, 0.794);
    -webkit-transform: scale(0.7, 0.7);
            transform: scale(0.7, 0.7);
    -webkit-animation: expandedanimation 1s ease-in;
            animation: expandedanimation 1s ease-in;
}
